import React, { useEffect, useState } from 'react';
import Button from "../button/Button";
import "./MainPage.css"
import Progress from '../progress/Progress';
import axios from 'axios';

const MainPage = () => {

    const [inProgress, setInProgress] = useState(false);

    const handleClick = () => {
		axios.get(`http://localhost:8080/performanceComparison/start`);
        setInProgress(true);
    }

    const handleReindexClick = () => {
        setInProgress(false);
    }
    
    return (
        <div>
            {inProgress ? (
                    <Progress onClick={handleReindexClick}></Progress>
                ) : (
                    <div className="main-page-container">
                    <h1>Inverted index performance comparison</h1>
                    <div className="button">
                        <Button text={"Get result"} onClick={handleClick}></Button>
                    </div>
                </div>
                )
            }
        </div>   
    );
}

export default MainPage;