import React, { useEffect, useState } from 'react';
import "./Progress.css"
import MyChart from '../main-chart/MyChart';
import Button from '../button/Button';
import axios from 'axios';
import MyBarChart from '../bar-chart/MyBarChart';

const Progress = ({onClick}) => {

    const [result, setResult] = useState({});

    useEffect(() => {
        axios.get('http://localhost:8080/performanceComparison/get')
            .then((res) => {
                setResult(res.data);
                console.log(res.data);
            });
    });
    
    return (
        <div>
            {Object.keys(result).length === 0 ? (
                <div>
                    <img src="/cat.gif" alt="Loading..." style={{ display: 'block', margin: 'auto' }} />
                    <p className='text'>Indexing in progress...</p>
                </div>
                ) : (
                    <div className='chart-container'>
                        <MyChart data={result}></MyChart>
                        <div className='reindex-button'>
                            <Button text={"Reindex"} onClick={onClick} ></Button>
                        </div>
                        <MyBarChart data={result}></MyBarChart>
                    </div>
                )
            }
        </div>   
    );
}

export default Progress;