import React from 'react';
import {
  BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer
} from 'recharts';
import { Typography } from '@mui/material';
import './MyBarChart.css'

const MyBarChart = ({ data }) => {
    const barColors = ['#a064a1', '#8884d8', '#82ca9d', '#ffc658', '#ff8042', '#0088FE', '#67d5b5', '#ff9f40', '#70a1ff'];
        
    const getBarChartData = (fileSize) => {
        return data[fileSize].map(({ threadsNum, executionTime }) => ({
            threadsNum,
            executionTime
        }));
    };
        
    return (
        <div className="grid-container">
        {Object.keys(data).map((fileSize, index) => (
            <div key={fileSize} style={{ marginBottom: '10px' }}>
                <Typography variant="h6" style={{ textAlign: 'center', fontFamily: "Montserrat", fontWeight: 700}}>
                    {`${fileSize} bytes input performance`}
                </Typography>
                <ResponsiveContainer width="100%" height={300}>
                <BarChart
                    data={getBarChartData(fileSize)}
                    margin={{
                        top: 20, right: 30, left: 20, bottom: 30,
                    }}
                >
                <CartesianGrid strokeDasharray="3 3" />
                <XAxis 
                    dataKey="threadsNum" 
                    label={{ value: 'Number of Threads', position: 'center', offset: -10, dy: 20 }}
                />
                <YAxis 
                    label={{ value: 'Execution Time (ms)', angle: -90, position: 'center', offset: 0,  dx: -20 }}
                />
                <Tooltip />
                <Bar dataKey="executionTime" fill={barColors[index % barColors.length]} />
                </BarChart>
                </ResponsiveContainer>
            </div>
            ))}
        </div>
    );
};
        
export default MyBarChart;