import React from 'react';
import {
  LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer
} from 'recharts';
import { Typography } from '@mui/material';
import './MyChart.css';

const MyChart = ({ data }) => {
	const chartData = [];
	const lineColors = ['#a064a1', '#8884d8', '#82ca9d', '#ffc658', '#ff8042', '#0088FE', '#67d5b5', '#ff9f40', '#70a1ff'];
        
	Object.entries(data).forEach(([fileSize, values], index) => {
	  values.forEach(({ threadsNum, executionTime }) => {
	    const entry = chartData.find(e => e.threadsNum === threadsNum);
	    if (entry) {
	      	entry[`${fileSize}bytes`] = executionTime;
	    } else {
	      	chartData.push({ threadsNum, [`${fileSize}bytes`]: executionTime });
	    }
	  });
	});
        
	const lines = Object.keys(data).map((fileSize, index) => (
	  <Line
	    key={fileSize}
	    type="monotone"
	    dataKey={`${fileSize}bytes`}
	    stroke={lineColors[index % lineColors.length]}
	    activeDot={{ r: 8 }}
	    strokeWidth={2}
	    dot={{ fill: lineColors[index % lineColors.length] }}
	  />
	));
        
	const renderCustomLegend = () => (
	  <div style={{ textAlign: 'center', marginTop: '10px', marginLeft: "50px" }}> 
	    {Object.keys(data).map((fileSize, index) => (
	      <span key={fileSize} style={{ color: lineColors[index % lineColors.length], marginRight: '15px' }}>
	         	{fileSize}bytes
	      </span>
	    ))}
	  </div>
	);

	const CustomTooltip = ({ active, payload, label }) => {
		if (active && payload && payload.length) {
		    return (
		        <div className="custom-tooltip">
			  	<p className="label">{`Threads: ${label}`}</p>
					{payload.map((entry, index) => (
						<p key={index} style={{ color: entry.color }}>
					{`${entry.name.split('bytes')[0]}bytes: ${entry.value}ms`}
			    </p>
			  	))}
		        </div>
		    );
		}
		return null;
	};
	  
        
	return (
		<div style={{ width: '100%', height: '100%' }}>
			<div className='heading'>
			<Typography variant="h4" style={{ textAlign: 'center', fontFamily: "Montserrat", fontWeight: 700}}>Inverted index performance comparison</Typography>
			<Typography variant="subtitle2" style={{ textAlign: 'center', marginTop: "20px", fontFamily: "Montserrat"}}>The chart of the dependence of execution time on the number of threads and the input size</Typography>
			</div>
			<ResponsiveContainer width="100%" height={480}>
			<LineChart
				data={chartData}
				margin={{
			top: 5, right: 30, left: 20, bottom: 30, 
				}}
			>
			<CartesianGrid strokeDasharray="3 3" />
			<XAxis dataKey="threadsNum" label={{ value: 'Number of Threads', position: 'center', offset: -10, dy: 20 }} />
			<YAxis label={{ value: 'Execution Time (ms)', angle: -90, position: 'center', offset: 0, dx: -20 }} />
				{lines}
			<Tooltip content={<CustomTooltip />} />
			</LineChart>
			</ResponsiveContainer>
			{renderCustomLegend()}
		</div>
	);
};
        
export default MyChart;